export default "<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">template</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h2</span>&gt;</span>Canvas学习：Canvas 绘制线段<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h2</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>\n      在 Canvas 中，我们可以使用\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>moveTo()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>lineTo()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>stroke()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>strokeRect()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>strokeText()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>fill()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>fillRect()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>fillText()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>等方法来绘制图形。\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>其中最常见也是最基础的便是线段的绘制了。<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">canvas</span> <span class=\"hljs-attr\">ref</span>=<span class=\"hljs-string\">&quot;canvas&quot;</span> <span class=\"hljs-attr\">width</span>=<span class=\"hljs-string\">&quot;300&quot;</span> <span class=\"hljs-attr\">height</span>=<span class=\"hljs-string\">&quot;200&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">canvas</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h3</span>&gt;</span>总结：<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h3</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>moveTo()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>lineTo()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>、和\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>stroke()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>可以很轻易的在Canvas中绘制出实线（线段）。\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>\n      画一个线段，但是它并不是真的绘制出来，而是将线段的起点和终点保存起来，当我们调用\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>stroke()<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>时，才真正的绘制出来。\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">template</span>&gt;</span>\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span> <span class=\"hljs-attr\">lang</span>=<span class=\"hljs-string\">&quot;ts&quot;</span>&gt;</span><span class=\"language-javascript\">\n<span class=\"hljs-comment\">/* eslint-disable no-param-reassign */</span>\n\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-title class_\">Vue</span> <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">&#x27;vue&#x27;</span>;\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> <span class=\"hljs-title class_\">Vue</span>.<span class=\"hljs-title function_\">extend</span>({\n  <span class=\"hljs-title function_\">mounted</span>(<span class=\"hljs-params\"></span>) {\n    <span class=\"hljs-keyword\">const</span> canvas = <span class=\"hljs-variable language_\">this</span>.<span class=\"hljs-property\">$refs</span>.<span class=\"hljs-property\">canvas</span> <span class=\"hljs-keyword\">as</span> HTMLCanvasElement;\n    <span class=\"hljs-keyword\">const</span> ctx = canvas.<span class=\"hljs-title function_\">getContext</span>(<span class=\"hljs-string\">&#x27;2d&#x27;</span>);\n    <span class=\"hljs-keyword\">if</span> (ctx) {\n      <span class=\"hljs-variable language_\">this</span>.<span class=\"hljs-title function_\">drawLine</span>(ctx);\n      <span class=\"hljs-variable language_\">this</span>.<span class=\"hljs-title function_\">drawStyledLine</span>(ctx);\n      <span class=\"hljs-variable language_\">this</span>.<span class=\"hljs-title function_\">drewMultiLine</span>(ctx);\n    }\n  },\n  <span class=\"hljs-attr\">methods</span>: {\n    <span class=\"hljs-comment\">// 绘制一条实线</span>\n    <span class=\"hljs-title function_\">drawLine</span>(<span class=\"hljs-params\">ctx: CanvasRenderingContext2D</span>) {\n      ctx.<span class=\"hljs-title function_\">beginPath</span>();\n      ctx.<span class=\"hljs-title function_\">moveTo</span>(<span class=\"hljs-number\">50</span>, <span class=\"hljs-number\">50</span>);\n      ctx.<span class=\"hljs-title function_\">lineTo</span>(<span class=\"hljs-number\">250</span>, <span class=\"hljs-number\">50</span>);\n      ctx.<span class=\"hljs-title function_\">stroke</span>();\n    },\n\n    <span class=\"hljs-comment\">// 绘制线段的样式</span>\n    <span class=\"hljs-title function_\">drawStyledLine</span>(<span class=\"hljs-params\">ctx: CanvasRenderingContext2D</span>) {\n      ctx.<span class=\"hljs-title function_\">beginPath</span>();\n      ctx.<span class=\"hljs-property\">strokeStyle</span> = <span class=\"hljs-string\">&#x27;#ff0000&#x27;</span>;\n      ctx.<span class=\"hljs-property\">lineWidth</span> = <span class=\"hljs-number\">5</span>;\n      ctx.<span class=\"hljs-title function_\">moveTo</span>(<span class=\"hljs-number\">50</span>, <span class=\"hljs-number\">100</span>);\n      ctx.<span class=\"hljs-title function_\">lineTo</span>(<span class=\"hljs-number\">250</span>, <span class=\"hljs-number\">100</span>);\n      ctx.<span class=\"hljs-title function_\">stroke</span>();\n    },\n\n    <span class=\"hljs-comment\">// 绘制多条线段</span>\n    <span class=\"hljs-title function_\">drewMultiLine</span>(<span class=\"hljs-params\">ctx: CanvasRenderingContext2D</span>) {\n      ctx.<span class=\"hljs-title function_\">beginPath</span>();\n      ctx.<span class=\"hljs-title function_\">moveTo</span>(<span class=\"hljs-number\">50</span>, <span class=\"hljs-number\">150</span>);\n      ctx.<span class=\"hljs-property\">strokeStyle</span> = <span class=\"hljs-string\">&#x27;#ff00ff&#x27;</span>;\n      ctx.<span class=\"hljs-title function_\">lineTo</span>(<span class=\"hljs-number\">100</span>, <span class=\"hljs-number\">200</span>);\n      <span class=\"hljs-comment\">// lineTo 可以多次调用，每次调用都会新增一个端点</span>\n      ctx.<span class=\"hljs-title function_\">lineTo</span>(<span class=\"hljs-number\">250</span>, <span class=\"hljs-number\">150</span>);\n      ctx.<span class=\"hljs-title function_\">stroke</span>();\n    },\n  },\n});\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n"